import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";

const renderField = ({
  input,
  label,
  type,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormSalesman extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    switch (this.props.from) {
      case "transaksi":
        return (
          <form>
            <div className="row">
              <div className="col-lg-12">
                <div className="form-group">
                  <label htmlFor="">.</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisPenjualan"
                        component="input"
                        type="radio"
                        value="penjualan"
                        className="mr-2"
                      />
                      Penjualan
                    </label>
                    <label>
                      <Field
                        name="jenisPenjualan"
                        component="input"
                        type="radio"
                        value="discount"
                        className="mr-2"
                      />
                      Discount Penjualan
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <div className="form-g">
                  <label htmlFor="">Salesman</label>
                  <Field
                    name="salesman"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="startDate"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="endDate"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12 mt-2">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      case "close":
        return (
          <form>
            <div className="row">
              <div className="col-lg-4">
                <div className="form-g">
                  <label htmlFor="">Salesman</label>
                  <Field
                    name="salesman"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="startDate"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="endDate"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12 mt-2">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      case "perFaktur":
        return (
          <form>
            <div className="row">
              <div className="col-lg-6">
                <div className="form-g">
                  <label htmlFor="">Salesman</label>
                  <Field
                    name="salesman"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="form-group">
                  <label htmlFor="">Sortir Berdasarkan</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="sortBy"
                        component="input"
                        type="radio"
                        value="tanggal"
                        className="mr-2"
                      />
                      Tanggal
                    </label>
                    <label>
                      <Field
                        name="sortBy"
                        component="input"
                        type="radio"
                        value="discopelangganunt"
                        className="mr-2"
                      />
                      Pelanggan
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="startDate"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="endDate"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12 mt-2">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      case "perCustomer":
        return (
          <form>
            <div className="row">
              <div className="col-lg-6">
                <div className="form-g">
                  <label htmlFor="">Salesman</label>
                  <Field
                    name="salesman"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="form-g">
                  <label htmlFor="">Area</label>
                  <Field
                    name="area"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="startDate"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="endDate"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12 mt-2">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      case "perBarang":
        return (
          <form>
            <div className="row">
              <div className="col-lg-6">
                <div className="form-g">
                  <label htmlFor="">Salesman</label>
                  <Field
                    name="salesman"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="form-g">
                  <label htmlFor="">Kadar</label>
                  <Field
                    name="kadar"
                    component="select"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </Field>
                </div>
              </div>
              <div className="col-lg-4">
                <Field
                  name="startDate"
                  label="Tanggal Awal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="endDate"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12 mt-2">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

FormSalesman = reduxForm({
  form: "FormSalesman",
  enableReinitialize: true,
})(FormSalesman);
export default connect()(FormSalesman);
