import React, { Component } from "react";
import FormSalesman from "./Component/FormSalesman";

class LaporanSalesman extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#transaksi-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Transaksi Penjualan
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#close-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link "
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Close Penjualan</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#perFaktur-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Omzet Per Faktur
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#perCustomer-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Omzet Per Customer
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#perBarang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Omzet Per Barang
                      </span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show active" id="transaksi-tab">
                    <FormSalesman from="transaksi" />
                  </div>
                  <div className="tab-pane" id="close-tab">
                    <FormSalesman from="close" />
                  </div>
                  <div className="tab-pane" id="perFaktur-tab">
                    <FormSalesman from="perFaktur" />
                  </div>
                  <div className="tab-pane" id="perCustomer-tab">
                    <FormSalesman from="perCustomer" />
                  </div>
                  <div className="tab-pane" id="perBarang-tab">
                    <FormSalesman from="perBarang" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LaporanSalesman;
